<template>
    <div id="app" style="max-width: 500px;">
        <layoutVue ref="layoutVue">
            <template v-slot:body>
                <router-view></router-view>
            </template>
        </layoutVue>
        <DialogRegisterCouple ref="DialogRegisterCouple" />
    </div>
</template>


<script>
    import layoutVue from './components/Layout.vue';
    import DialogRegisterCouple from './components/DialogRegisterCouple.vue';

    export default {
        name: 'App',
        components: {
            layoutVue,
            DialogRegisterCouple,
        },
        data() {
            return {
                socket: null,
                isAdmin: false,
            }
        },
        mounted: function() {
            let vm = this;
            if(!vm.$store.state.user.socketId) {
                vm.$router.push('/login');
            }
        }
    }
</script>