<template>
<div id="settingVue" >
    <van-button block @click="logOut" type="primary" native-type="submit">
        {{$t('Logout')}}
    </van-button>
    <van-button block @click="i18Change" type="primary" native-type="submit">
        변경
    </van-button>
    <van-button v-if="$store.state.user.isAdmin" type="primary" native-type="submit">
        어드민 페이지로 이동
    </van-button>
    <van-action-sheet v-model:show="isShow" :actions="actions" @select="onSelect" />
</div>
</template>

<script>
import { showNotify } from 'vant';
export default {
    name: 'settingVue',
    data() {
        return {
            isShow: false,
            actions: [
                { name: 'English' },
                { name: 'Korea' },
            ],
        }
    },
    methods: {
        onSelect: function(arg) {
            let vm = this;
            let name = arg.name;
            if(name == 'English') {
                vm.$i18n.locale = "en";
            } else {
                vm.$i18n.locale = "ko";
            }
            showNotify({ type: 'success', message: '변경 완료!' });
            vm.isShow = false;
        },
        logOut: function() {
            let vm = this;
            // vm.$store.state.user = null;
            vm.$store.commit("setUser", {
                userId: '',
                socketId: '',
                phoneNumber: '',
            });
            vm.$store.commit("setToken", "");
            vm.$store.commit("setCouple", "");
            vm.$store.state.setCookie("token", "");
            vm.$router.push("/login");
        },  
        i18Change: function() {
            let vm = this;
            vm.isShow = true;
        },
    },
    mounted() {
        let vm = this;
        if(!vm.$store.state.user.socketId) {
            vm.$router.push('/login');
        }
    }
}
</script>