import { createWebHistory, createRouter } from "vue-router";
import HomeVue from "../views/Home.vue";
import LoginVue from "../views/login/Login.vue";
import WaitingVue from "../views/login/Waiting.vue";
import ErrorVue from "../views/error/Error.vue";
import SettingVue from "../views/setting/Setting.vue";
import ChatVue from "../views/chat/Chat.vue";

/* admin */
import AdminDashboardVue from "../views/admin/dashboard.vue";
import AdminUserVue from "../views/admin/user.vue";
import AdminGroupVue from "../views/admin/group.vue";
const routes = [
    { path: "/", name: "home", component: HomeVue, },
    { path: "/chat", name: "chat", component: ChatVue, },
    { path: "/login", name: "Login", component: LoginVue, },
    { path: "/waiting", name: "Waiting", component: WaitingVue, },
    { path: "/error", name: "Error", component: ErrorVue, },
    { path: "/setting", name: "Setting", component: SettingVue, },
    { path: "/home", name: "Home", component: HomeVue, },
    { path: "/admin/dashboard", name: "AdminDashboard", component: AdminDashboardVue, },
    { path: "/admin/user", name: "AdminUser", component: AdminUserVue, },
    { path: "/admin/group", name: "AdminGroup", component: AdminGroupVue, },
    { path: "/:pathMatch(.*)*", redirect: "/error" },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

router.beforeEach((to, from) => {
    console.error(`from: ${from.fullPath}, to: ${to.fullPath}`);
});

export default router;