<template>
    <div id="layoutVue" style="height: 100%;" ref="layoutVue" >
        <div style="display: flex; flex-direction: column; height: 100%;">
            <div v-if="!unShowPage.hasOwnProperty($route.name)" style="border-bottom: 1px solid #aaa;">
                <van-nav-bar :title="item_list[active].label" 
                    left-text="Back" left-arrow
                    @click-left="onClickLeft"/>
            </div>
            <div style="height: 100%;" :style="{height: (height-130)+'px'}">
                <slot name="body">

                </slot>
            </div>
        </div>
        <div v-if="!unShowPage.hasOwnProperty($route.name)">
            <van-tabbar v-model="active" style="border-top: 1px solid #aaa; height: 72px;"
                @change="onChange" ref="footer">
                <template v-for="(row, idx) in item_list" >
                    <template v-if="row.hasOwnProperty('isCustom') && row.isCustom">
                        <van-tabbar-item :key="idx">
                            <template #icon="props">
                                <template v-if="$store.state.couple.socketId">
                                    <van-badge :content="100" max="99">
                                        <img src="/images/doge_v3.png" 
                                            style="width: 50px !important; height: 50px !important;"/>
                                    </van-badge>
                                </template>
                                <template v-else>
                                    <van-badge :content="200" max="99">
                                        <img src="/images/도지.png" 
                                            style="width: 50px !important; height: 50px !important;"/>
                                    </van-badge>
                                </template>
                            </template>
                        </van-tabbar-item>
                    </template>
                    <template v-else>
                        <van-tabbar-item :icon="row.icon" :key="idx">{{$t(row.label)}}</van-tabbar-item>
                    </template>
                </template>
            </van-tabbar>
        </div>
    </div>
</template>

<script>
export default {
    name: 'layoutVue',
    data() {
        return {
            height: 0,
            isShow: true,
            unShowPage: {
                Login: true,
                Error: true,
                Waiting: true,
                Admin: true,
            },
            item_list: [
                { icon: 'home-o', label: 'Home', url: '/home', name: 'Home' },
                { icon: 'search', label: 'Search', url: '/search', name: 'Search', },
                { icon: 'chat-o', label: 'chat', url: '/chat', name: 'Chat', isCustom: true },
                { icon: 'friends-o', label: 'Community', url: '/community', name: 'Community',},
                { icon: 'setting-o', label: 'Setting', url: '/setting', name: 'Setting', },
            ],
            active: 0,

            showLeft: false, 
            url_list: [
                { icon: 'chat-o', label: 'User', url: '/admin/user', name: 'AdminUser', isActive: false },
                { icon: 'setting-o', label: 'Group', url: '/admin/group', name: 'AdminGroup', isActive: false }
                // { icon: 'user-o', label: 'Dashboard', url: '/admin/dashboard', name: 'AdminDashboard', isActive: false },
            ],
        }
    },
    props: {
    },
    computed: {
        isAdmin: function() {
            return this.$store.state.user.isAdmin;
        },
    },
    methods: {
        onChange(idx) {
            let vm = this;
            let item = vm.item_list[idx];
            vm.$router.push(item.url);
        },
        onClickLeft: function() {
            let vm = this;
            let back_route = vm.$router.options.history.state.back;
            if(back_route == '/login') { return; }
            vm.$router.back();
        },
    },
    mounted() {
        let vm = this;
        vm.$router.afterEach((to, from) => {
            let routeName = to.name;
            let idx = vm.item_list.findIndex(x => x.name == routeName);
            if(idx >= 0) { vm.active = idx; }
        });
    },
    created() {
        let vm = this;
        vm.height = window.screen.height;
    }
}
</script>