<template>
    <div id="ChatVue" style="height: 100%;">
        <div style="display: flex; flex-direction: column; height: 100%; background-color: #eee;">
            <div style="height: 100%;" class="pd10">
                <div style="display: flex; flex-direction: row;">
                    <div>
                        <van-image round width="60" height="60" fit="cover"
                            src="https://fastly.jsdelivr.net/npm/@vant/assets/cat.jpeg"/>
                    </div>
                    <div style="display: flex; flex-direction: column; margin-left: 5px;">
                        <div>{{item[0].displayName}}</div>
                        <div v-for="(row, idx) in item" :key="idx">
                            <div class="bg-white mt5" style="border-radius: 10px; padding: 10px 5px;">
                                {{ row.message }}
                            </div>
                            <div v-if="idx == item.length-1" style="display: flex; align-items: end;">
                                {{ row.sendDate }}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style="height: 50px;">
                <van-field v-model="message" center clearable placeholder="Say something...">
                    <template #button>
                        <van-icon name="guide-o" size="40px" @click="send_message" />
                    </template>
                </van-field>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ChatVue',
    data() {
        return {
            socket: null,
            message: '',
            item: [
                { displayName: '상대방', message: 'ㅋㅋㅋㅋㅋㅋ', sendDate: '2023-01-03T12:12:12'},
                { displayName: '상대방', message: '진짜로?', sendDate: '2023-01-03T12:12:12' },
                // { displayName: '상대방', message: '그게맞나그게맞나그게맞나그게맞나그게맞나그게맞나그게맞나그게맞나', sendDate: '2023-01-03T12:12:12'},
            ],
            currentUser: {
                id: 1,
                type: {},
                required: true
            },
            value: '',
        }
    },
    methods: {
        send_message: function() {
            let vm = this;
            console.log("send_message:");
            vm.socket.emit(`/socket/send/message`, {
                message: vm.message,
                user: vm.$store.state.user,
            }, (data, err) => {
                if(data.success == 0) {
                    console.log(data);
                }
            });
        },
    },
    mounted: function() {
        let vm = this;
        vm.socket = vm.$store.state.socket;

        vm.socket.on(`/client/receive/message`, (data) => {
            vm.item.push(data);
            console.log("data:", data);
        });
    },
}
</script>