<template>
    <div id="AdminVue" style="height: 100%;">
        <div style="display: flex; flex-direction: column; height: 100%; background-color: #eee;">
            <van-tabs v-model:active="tab">
                <van-tab title="사용자별" name="user">
                    <van-swipe-cell v-for="(val, key) in user_dict" :key="key">
                        <van-cell :border="false" :title="key" :value="val.socketId" />
                        <template #right>
                            <van-button square type="danger" text="Disconnect" @click="shutdown(key)"/>
                        </template>
                    </van-swipe-cell>
                </van-tab>
                <van-tab title="그룹별" name="group">
                    그룹별
                </van-tab>
            </van-tabs>
        </div>
    </div>
</template>

<script>
import { showFailToast, showSuccessToast } from 'vant';
export default {
    name: 'AdminVue',
    data() {
        return {
            tab: "user",
            socket: null,
            user_list: [],
            user_dict: new Object(),
        }
    },
    methods: {
        shutdown: function(key) {
            let vm = this;
            vm.socket.emit(`/socket/admin/shutdown`, {
                key,
            }, (data, err) => {
                if(data.success) {
                    showSuccessToast({
                        message: data.message,
                        wordBreak: 'break-all',
                    });
                } else {
                    showFailToast({
                        message: data.message,
                        wordBreak: 'break-all',
                    });
                }
            });
        },
    },
    mounted: function() {
        let vm = this;
        if(!vm.$store.state.user.isAdmin) {
            vm.$router.push('/login');
        }
        vm.socket = vm.$store.state.socket;
        vm.socket.emit(`/socket/admin/clients/get_all`, {}, (data, err) => {
            vm.user_dict = data.user_dict;
        });

        vm.socket.emit(`/socket/admin/rooms/get_all`, {}, (data, err) => {
            console.log("data:", data);
        });

        vm.socket.on(`/client/admin/connect/user`, (data) => {
            vm.user_dict[data.phoneNumber] = {
                socketId: data.socketId
            };
        });

        vm.socket.on(`/client/admin/disconnect/user`, (data) => {
            if(Object.prototype.hasOwnProperty.call(vm.user_dict, data.phoneNumber) == true) {
                delete vm.user_dict[data.phoneNumber];
            }
        });
    },
}
</script>